body, div, main, section, html, nav {
  box-sizing: border-box;
}

html, body {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}

main {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}



