// Common styles for semantic nav
nav  {
  z-index: 10;

  > a {
    text-decoration: none;
    font-weight: 200;
    font-size: 21px;
    color: rgba(0, 0, 0, 0.65);

    &:hover {
      background: rgba(0, 0, 0, .15);
      color: rgba(0, 0, 0, 0.65);
      text-decoration: none;
    }
  }
}



