@mixin background-enter-animation($url) {
  &:before {
    content: ' ';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    background-repeat: no-repeat;
    background-image: url($url);
  }
  &:target:before {
    opacity: 1;
    transition: all 800ms;
  }
}



